.center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.green {
    color: #57d176;
}
.m {
    margin: 0 8px;
}

.color {
    color: rgb(118, 116, 120);
}

#main-wrapper {
    background: #ffffff;
    opacity: 1;
    .intro {
        background-color: #1c1171;
        .intro-content {
            &-top {
                display: flex;
                justify-content: space-between;
                align-items: center;
                background: #ffffff;
                padding: 15px;
                margin-top: 20px;
                border-radius: 10px;
                .Ititle {
                    font-size: 22px;
                    color: #333333;
                    font-family: Arial;
                    font-weight: 700;
                    margin-left: 10px;
                }
                .Ides {
                    color: #000000;
                    font-size: 18px;
                    font-family: Arial;
                }
            }
        }
        .intro-btn {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 20px;
            .introCard {
                font-size: 18px;
                color: white;
                font-weight: bold;
                font-family: Arial, Helvetica, sans-serif;
                margin-right: 15px;
                .Ititle {
                    font-weight: initial;
                    font-family: Arial, Helvetica, sans-serif;
                    font-size: 16px;
                }
                .amountC {
                    color: #d03639;
                }
            }
            .introButton {
                font-size: 20px;
                background: #fecb2e;
                border-radius: 10px;
                padding: 6px 15px 6px 15px;
                font-weight: bold;
                font-family: Arial, Helvetica, sans-serif;
                transition: all 0.3s;
                &:hover {
                    background: #d03639;
                }
                a {
                    color: #ffffff;
                }
            }
        }
        .intro-bottom {
            display: flex;
            align-items: center;
            margin-top: 10px;
        }
    }
    .charts {
        display: flex;
        align-items: center;
        background-color: #0b1825;
        width: 100%;
        margin: 0 auto;
        min-width: 1040px;
        overflow-y: auto;
        height: 180px;
        .chartss {
            display: flex;
            justify-content: space-around;
        }
        .chart {
            width: 232px;
            border: 1px solid #242b32;
            height: 112px;
            margin: 12px 10px 0 0;
            z-index: 1;
            overflow: hidden;
            position: relative;
            border-radius: 4px;
            transition: all 0.6s;
            &:hover {
                border: 1px solid #3375e0;
                transform: translateY(-10px);
                transition: border-color 0.5s;
            }
            .recharts-responsive-container {
                transform: translateY(10px);
            }
            .charts_text {
                top: 0;
                left: 0;
                color: #242b32;
                width: 100%;
                cursor: pointer;
                padding: 16px 20px 7px 16px;
                z-index: 2;
                position: absolute;
                span {
                    color: #fff;
                    display: inline-block;
                    font-size: 1.14285rem;
                    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
                    font-weight: bold;
                    line-height: 23px;
                    letter-spacing: 0.001em;
                }
                div {
                    float: right;
                    display: inline-block;
                    font-size: 16px;
                    font-weight: bold;
                    line-height: 23px;
                }
                strong {
                    display: inline-block;
                    font-size: 24px;
                    margin-top: 8px;
                    font-weight: bold;
                    line-height: 35px;
                }
                p {
                    color: #d3d5d6;
                    font-size: 1rem;
                    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
                    font-weight: 400;
                    line-height: 22px;
                    letter-spacing: 0.01071em;
                }
            }
        }
    }
    .Altcoin {
        margin-top: 70px;
        .Altcoin-title {
            color: #000000;
            font-size: 32px;
            font-family: "Roboto", "Helvetica", "Arial", sans-serif;
            margin-bottom: 20px;
        }
        .Altcoins {
            padding: 0 25px;
            border: 1px solid;
            box-shadow: 1px 0px 5px 1px;
        }

        &_List {
            border-bottom: 1px solid;
            display: flex;
            justify-content: space-around;
            align-items: center;
            padding: 20px;
            .span {
                font-size: 18px;
            }

            &-center {
                width: 100%;
                text-align: center;
                font-size: 18px;
                .bg {
                    background-color: #eaf6f3;
                    padding: 10px;
                    border-radius: 5px;
                }
                a {
                    padding: 10px 15px;
                    border-radius: 5px;
                    background-color: #1bb367;
                    color: #ffffff;
                }
            }
            .Altcoins_svg {
                svg {
                    height: 21px;
                    width: 101px;
                }
            }
        }
        .Altcoin_show {
            padding: 20px;
            text-align: center;
            font-size: 18px;
        }
        .Carousel {
            margin-top: 30px;
            &_list {
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #00008b;
                height: 100px;
                width: 100%;
                margin: 0 15px;
                a {
                    font-size: 32px;
                    color: #ffff;
                    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
                }
            }
        }
    }
    .getstart {
        .section-title {
            h2 {
                color: #000000;
            }
        }
        .getstart-content {
            h3 {
                color: #000000;
            }
        }
    }

    .content-body {
        margin-top: 40px;
        .tradingview {
            flex: 100%;
            max-width: 100%;
            .tradingview-widget-container {
                height: 500px;
            }
        }
        .history {
            max-width: 100%;
            flex: 100%;
        }
    }
}
