.header {
    background: #0e0838;
    .navigation {
        .navbar {
            .collapse {
                justify-content: initial;
            }
            .btn-header {
                color: #ffffff;
                &.active,
                &:hover {
                    background: #fecb2e;
                }
            }
            .lang {
                display: flex;
                .langimg {
                    width: 25px;
                    height: 25px;
                    &:last-child {
                        margin: 0 8px;
                    }
                }
            }
        }
    }
}
